@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/pt-mono-regular.woff2") format("woff2"), url("../fonts/pt-mono-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
  font-display: swap; }

*,
*::before,
*::after {
  box-sizing: border-box; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.button {
  display: inline-flex;
  position: relative;
  align-items: center;
  justify-content: center;
  padding: 30px 40px;
  font-family: "Roboto Condensed", "Verdana", sans-serif;
  background-color: #00b2ff;
  border-style: none;
  cursor: pointer;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: #ffffff;
  border-radius: 5px; }
  .button--call-back {
    padding: 14px 10px 13px;
    min-width: 180px;
    background-color: transparent;
    border: 1px solid #ffffff;
    letter-spacing: 0.05em; }
  .button--feedback {
    padding: 19px 29px 17px;
    min-width: 407px;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0.05em; }

@media (max-width: 1023px) {
  .button--feedback {
    padding-left: 14px;
    padding-right: 14px;
    min-width: 379px; } }

.container {
  max-width: 1555px;
  margin: 0 auto;
  padding: 0 30px; }
  @media (max-width: 1023px) {
    .container {
      max-width: 1073px;
      padding: 0 25px; } }
  @media (max-width: 767px) {
    .container {
      max-width: 799px;
      padding: 0 16px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page {
  height: 100%;
  font-family: "PT Mono", "Georgia", serif;
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  line-height: 22px; }

.page-body {
  display: flex;
  flex-direction: column;
  align-content: flex-start;
  position: relative;
  height: 100%;
  margin: 0;
  min-width: 320px;
  min-height: 100%; }
  .page-body--menu-opened {
    overflow-y: hidden; }

.page-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding-top: 33px; }

.page-header__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center; }

.page-header__logo {
  margin-right: auto; }

.page-header__button {
  margin-left: 38px; }

@media (max-width: 1023px) {
  .page-header {
    padding-top: 20px; }
  .page-header__button {
    display: none; } }

@media (max-width: 767px) {
  .page-header {
    padding-top: 17px; } }

.user-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center; }

.user-nav__item {
  margin-right: 88px;
  line-height: 18px; }
  .user-nav__item:last-child {
    margin-right: 0; }

.user-nav__link {
  display: flex;
  color: #ffffff;
  align-items: center; }
  .user-nav__link--tel .user-nav__icon {
    margin-right: 16px; }
  .user-nav__link--mail .user-nav__icon {
    margin-right: 20px; }

@media (max-width: 1023px) {
  .user-nav__item {
    margin-right: 33px; }
  .user-nav__link span {
    display: none; }
  .user-nav__link--tel .user-nav__icon {
    margin-right: 0;
    width: 24px;
    height: 24px; }
  .user-nav__link--mail .user-nav__icon {
    margin-right: 0;
    width: 26px;
    height: 19px; } }

